import React, { useEffect, useState } from 'react';
import { Button, Card, CardActions, CardContent, Grid, TextField, Typography } from '@material-ui/core';
import { CreditCard } from '@material-ui/icons';
import InputMask from 'react-input-mask';
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import { useAlerta } from '../../contexts/AlertaCtx';
import { useMapa } from '../../contexts/MapaCtx';
import { useSolicitacao } from '../../contexts/SolicitacaoCtx';
import { useGeral } from '../../contexts/GeralCtx';
import { limpaObjeto } from '../../utils/funcoes';
import * as service from '../../services/CheckoutMPService';
import { TIPO_ERRO } from '../../utils/global';
import Cabecalho from './Cabecalho';
import schema from './CheckoutSch';
import Styles from './Styles';
import PagesCss from '../PagesCss';


export default function Checkout() {

  const classes = Styles();
  const css = PagesCss();
  const { estiloDeCampo } = useGeral();
  const { setConteudo } = useAlerta();
  const { setMapa } = useMapa();
  const { dados, pgto, cartao, setCartao, setTela } = useSolicitacao();
  const [nova, setNova] = useState(false);  
  const [cancelado, setCancelado] = useState(false);
  const [credencial, setCredencial] = useState(true);  
  const [bandeira, setBandeira] = useState('');

  const valoresIniciais = {
    chkNome: cartao.chkNome,
    chkEmail: cartao.chkEmail,
    chkNrCartao: cartao.chkNrCartao,
    chkCodSeguranca: cartao.chkCodSeguranca,
    chkMes: cartao.chkMes,
    chkAno: cartao.chkAno
  };

  const { handleSubmit, control, errors, setValue, reset } = useForm({
    mode: 'all',
    defaultValues: valoresIniciais,
    resolver: yupResolver(schema)
  });

  useEffect(() => {
    if (credencial) {
      window.Mercadopago.setPublishableKey('TEST-b1cb2d8c-5166-45be-bed1-865efbe208c6');
      window.Mercadopago.getIdentificationTypes();
      setCredencial(false);
    }
  }, [credencial, setCredencial])

  function onBlurCartao(ev) {
    var bin = ev.currentTarget.value.replace(/\D/g, '');
    if (bin.length >= 6) {
      window.Mercadopago.getPaymentMethod({
        "bin": bin.substring(0, 6),
      }, defineMetodoPgto);
    } else {
      setBandeira('');
    }
  }

  function defineMetodoPgto(status, response) {
    if (status === 200) {
      setBandeira(response[0].thumbnail);
      setCartao({
        ...cartao,
        idMeioPagamento: response[0].id,
        idTipoPagamento: response[0].payment_type_id,
      })
    } else {
      setConteudo({
        tipo: TIPO_ERRO,
        descricao: "Método de pagamento não encontrado",
        exibir: true
      });
    }
  }

  function inseriStatusPgto(respostaGateway) {
    var statusPgto = null;

    if (respostaGateway.status === "rejected") {
      statusPgto = "N";
    } else {
      statusPgto = "A";
    }

    service.inseriStatusPgto(`/pagamentos-status`, pgto.idPagamento, statusPgto)
      // .then(response => {
      //   console.log(response);
      // })
      .catch(error => {
        setConteudo({
          tipo: TIPO_ERRO,
          descricao: error.response.data.detalhe,
          exibir: true
        });
      });
  }

  function novaSolicitacao(){
    limpaObjeto(dados);
    limpaObjeto(pgto);
    limpaObjeto(cartao);
    setMapa({
      origem: null,
      destino: null,
      modoViagem: "DRIVING",
    });
    setNova(false);
    setCancelado(false);
    setTela(1);
  }

  function cancelar() {
    service.inseriStatusEntrega(`/entregas-status`, pgto.idEntrega, "CA")
    .then(response => {
      setNova(true);
      setCancelado(false);
      limpaObjeto(cartao);
      reset(valoresIniciais);
      setBandeira('');
    })
    .catch(error => {
      setNova(false);
      setCancelado(false)
      setConteudo({
        tipo: TIPO_ERRO,
        descricao: error.response.data.detalhe,
        exibir: true
      });
    });    
  }

  function checkoutMP() {
    const objeto = {
      cliente: dados.cliente,
      idGateway: pgto.idGateway,
      chkNrCartao: cartao.chkNrCartao,
      chkMes: cartao.chkMes,
      chkAno: cartao.chkAno,
      chkCodSeguranca: cartao.chkCodSeguranca,
      chkNome: cartao.chkNome,
      vlrPercurso: pgto.qtdeRepeticao ? pgto.vlrPercurso * pgto.qtdeRepeticao : pgto.vlrPercurso,
      vlrProduto: pgto.qtdeRepeticao ? pgto.vlrProduto * pgto.qtdeRepeticao : pgto.vlrProduto,
      parcelas: 1,
      idMeioPagamento: cartao.idMeioPagamento,
      idPagamento: pgto.idPagamento,
    };

    service.insere(`/pagamentos/checkout`, objeto)
    .then(response => {
      inseriStatusPgto(response.data);
      if (response.data.status === "rejected") {
        setConteudo({
          tipo: TIPO_ERRO,
          descricao: "Não foi possível processar o pagamento." +
            "Descrição do problema: " +
            response.data.response,
          exibir: true
        });
        setNova(false);
        setCancelado(true);
      } else {
        setTela(10);
      }
    })
    .catch(error => {
      setConteudo({
        tipo: TIPO_ERRO,
        descricao: error.response.data.detalhe,
        exibir: true
      });
    });
  }

  function submitForm(data) {
    checkoutMP();
  }

  return (
    <div>
      <form onSubmit={handleSubmit(submitForm)} >
        <Card>
          <Cabecalho/>
          <CardContent>
            <Grid container direction="row">
              <Grid item xs={12} >
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                  <div className={classes.tituloPagina}>
                    <CreditCard style={{ color: '#FEC601', marginRight: '6px' }} />
                    <Typography variant="subtitle1">Checkout Cartão de Crédito</Typography>
                  </div>
                  <div className={classes.subtituloPagina}>
                    <Typography variant="body2">Informe os dados do cartão de crédito</Typography>
                  </div>
                </div>
              </Grid>
              {cancelado &&
                <div className={classes.mensagem}>
                  <Typography variant="subtitle2" style={{color: 'red'}}>
                    Não foi possível concretizar o pagamento.
                  </Typography>
                  <Typography variant="caption">
                    Você pode tentar proceder o pagamento utilizando outro cartão.
                  </Typography>
                  <Typography variant="caption">
                    Caso prefira, <b>cancele</b> a solicitação.
                  </Typography>
                </div>            
              }
              {nova &&
                <div className={classes.mensagem}>
                  <Typography variant="subtitle1"><b>Pronto, solicitação cancelada!</b></Typography>
                </div>
              }
              <Grid container spacing={2} style={{ margin: '2px', padding: '15px 5px', backgroundColor: '#F8F8F9' }} >

                <Grid item xs={12} sm={6} md={6} >
                  <Controller
                    name="chkNome"
                    control={control}
                    render={({ error }) => (
                      <TextField
                        fullWidth
                        type="text"
                        label="Nome no Cartão"
                        variant={estiloDeCampo}
                        placeholder="EX.: Antônio S Morais"
                        InputLabelProps={{ shrink: true }}
                        inputProps={{ style: { textTransform: "uppercase" } }}
                        value={cartao.chkNome}
                        onChange={(e) => {
                          setValue('chkNome', e.target.value);
                          setCartao({ ...cartao, chkNome: e.target.value });
                        }}
                        error={!!errors.chkNome}
                        helperText={errors.chkNome ? errors.chkNome.message : ''}
                      />
                    )}
                  />
                </Grid>

                <Grid item xs={12} sm={6} md={6} >
                  <Controller
                    name="chkEmail"
                    control={control}
                    render={({ error }) => (
                      <TextField
                        fullWidth
                        type="text"
                        label="E-Mail"
                        variant={estiloDeCampo}
                        placeholder=""
                        InputLabelProps={{ shrink: true }}
                        inputProps={{ style: { textTransform: "lowercase" } }}
                        value={cartao.chkEmail}
                        onChange={(e) => {
                          setValue('chkEmail', e.target.value);
                          setCartao({ ...cartao, chkEmail: e.target.value });
                        }}
                        error={!!errors.chkEmail}
                        helperText={errors.chkEmail ? errors.chkEmail.message : ''}
                      />
                    )}
                  />
                </Grid>

                <Grid item xs={12} sm={6} md={6} >
                  <Controller
                    name="chkNrCartao"
                    control={control}
                    render={({ onChange, onBlur, error }) => (
                      <InputMask
                        mask="9999 9999 9999 9999"
                        maskChar=" "
                        disabled={false}
                        value={cartao.chkNrCartao}
                        onChange={(ev) => {
                          setCartao({ ...cartao, chkNrCartao: ev.target.value });
                          setValue('chkNrCartao', ev.target.value)
                        }}
                        onBlur={(ev) => onBlurCartao(ev)}
                      >
                        {(inputProps) =>
                          <TextField
                            {...inputProps}
                            fullWidth
                            type="text"
                            label="Número do Cartão"
                            variant={estiloDeCampo}
                            placeholder="9999 9999 9999 9999"
                            InputLabelProps={{ shrink: true }}
                            error={!!errors.chkNrCartao}
                            helperText={errors.chkNrCartao ? errors.chkNrCartao.message : ''}
                          />
                        }
                      </InputMask>
                    )}
                  />
                </Grid>

                <Grid item xs={4} sm={2} md={2} >
                  <Controller
                    name="chkCodSeguranca"
                    control={control}
                    render={({ onChange, onBlur, error }) => (
                      <InputMask
                        mask="9999"
                        maskChar=" "
                        disabled={false}
                        value={cartao.chkCodSeguranca}
                        onChange={(ev) => {
                          setCartao({ ...cartao, chkCodSeguranca: ev.target.value });
                          setValue('chkCodSeguranca', ev.target.value);
                        }}
                      //onBlur={ (ev)=>onBlurCep(ev) }
                      >
                        {(inputProps) =>
                          <TextField
                            {...inputProps}
                            fullWidth
                            type="text"
                            label="CV"
                            //name="origCep"                
                            variant={estiloDeCampo}
                            placeholder="9999"
                            InputLabelProps={{ shrink: true }}
                            error={!!errors.chkCodSeguranca}
                            helperText={errors.chkCodSeguranca ? errors.chkCodSeguranca.message : ''}
                          />
                        }
                      </InputMask>
                    )}
                  />
                </Grid>

                <Grid item xs={4} sm={2} md={2} >
                  <Controller
                    name="chkMes"
                    control={control}
                    render={({ error }) => (
                      <TextField
                        fullWidth
                        type="number"
                        label="Mês"
                        variant={estiloDeCampo}
                        placeholder="99"
                        InputLabelProps={{ shrink: true }}
                        inputProps={{ style: { textTransform: "uppercase" } }}
                        value={cartao.chkMes}
                        onChange={(e) => {
                          setValue('chkMes', e.target.value);
                          setCartao({ ...cartao, chkMes: e.target.value });
                        }}
                        error={!!errors.chkMes}
                        helperText={errors.chkMes ? errors.chkMes.message : ''}
                      />
                    )}
                  />
                </Grid>

                <Grid item xs={4} sm={2} md={2} >
                  <Controller
                    name="chkAno"
                    control={control}
                    render={({ error }) => (
                      <TextField
                        fullWidth
                        type="number"
                        label="Ano"
                        variant={estiloDeCampo}
                        placeholder="9999"
                        InputLabelProps={{ shrink: true }}
                        inputProps={{ style: { textTransform: "uppercase" } }}
                        value={cartao.chkAno}
                        onChange={(e) => {
                          setValue('chkAno', e.target.value);
                          setCartao({ ...cartao, chkAno: e.target.value });
                        }}
                        error={!!errors.chkAno}
                        helperText={errors.chkAno ? errors.chkAno.message : ''}
                      />
                    )}
                  />
                </Grid>

              </Grid>
              <Grid item xs={12}>
                <div style={{ marginTop: '10px', height: '50px' }}>
                  {bandeira &&
                    <img src={bandeira} alt="bandeira" />
                  }
                </div>
              </Grid>
            </Grid>
          </CardContent>
          <CardActions>
            <div style={{ display: 'flex', width: '100%', margin: '10px' }}>
              <div style={{ flex: 1 }} />
              { cancelado && 
                <Button
                variant="contained"
                color="primary"
                disableElevation
                className={css.formBotaoDeAcao}
                onClick={cancelar}
                > Cancelar
                </Button>
              }
              {nova && 
                <Button
                  variant="contained"
                  color="primary"
                  disableElevation
                  className={css.formBotaoDeAcao}
                  onClick={novaSolicitacao}
                > Nova Solicitação
                </Button>          
              }
              {(!nova) &&
                <Button
                  type="submit"
                  variant="contained"
                  color="primary"
                  disableElevation
                  className={css.formBotaoDeAcao}
                > Finalizar Pagamento
                </Button>
              }
            </div>
          </CardActions>
        </Card>
      </form>
    </div>
  )
}
